
#nav-wrap ul, #nav-wrap li, #nav-wrap a {
    margin: 0;
    padding: 0;
    border: none;
    outline: none;
}

/* nav-wrap */
#nav-wrap {
  font: 12px 'opensans-bold', sans-serif;
  width: 100%;
  text-transform: uppercase;
  letter-spacing: 2.5px;
  margin: 0 auto;
  z-index: 100;
  position: fixed;
  left: 0;
  top: 0;
}

.banner__text { 
  text-transform: none; 
}

/* hide toggle button */
#nav-wrap > a.mobile-btn { display: none; }

ul#nav {
  min-height: 48px;
  width: auto;
  text-align: center;
}

ul#nav li {
  position: relative;
  list-style: none;
  height: 48px;
  display: inline-block;
}

/* Links */
ul#nav li a {
  display: inline-block;
  padding: 8px 13px;
  line-height: 32px;
  text-decoration: none;
  text-align: left;
  color: black; 

   -webkit-transition: color .2s ease-in-out;
   -moz-transition: color .2s ease-in-out;
   -o-transition: color .2s ease-in-out;
   -ms-transition: color .2s ease-in-out;
   transition: color .2s ease-in-out;
}

ul#nav li a:active { background-color: transparent !important; }
ul#nav li.current a { color: #F06000; }

.banner__content {
  padding: 5px;
  display: flex;
  align-items: center;
}

.banner__text {
  flex-grow: 1;
  line-height: 1.4;
  position: relative;
}

.banner__close {
  background: none;
  border: none;
  cursor: pointer;
  position: relative;
}

.banner__text,
.banner__close > span {
  color: #000000;
  position: relative;
}

/* CSS for banner section */
.banner {
  text-align: center;
  margin-top: 50px; /* Adjust margin as needed */
}

.banner-text {
  color: #000000; 
}

.responsive-headline {
  font-size: 36px; 
  margin-bottom: 10px; 
  color: brown;
}

.social {
  list-style: none;
  padding: 0;
  text-align: center;
}

.social li {
  display: inline-block;
  margin: 0 10px;
}

.social li a {
  color: #000000; 
}

.banner-text hr {
  border-color: #000000;
  margin-top: 10px;
}

.navbar-container {
    position: relative;
    height: 500px;
}

.opaque {
  background-color: rgb(0, 0, 0);
}
